import { _decorator, Component, Node, Vec3, systemEvent, SystemEventType, EventTouch, Vec2, CCInteger } from 'cc';
import { PiecesPool } from '../Poolable/PiecesPool';
const { ccclass, property } = _decorator;

@ccclass('GridGenerator')
export class GridGenerator extends Component {
    @property(PiecesPool)
    private piecesPool: PiecesPool = null;
   
    private PIECE_CONTENT_SIZE: number = 100; //dinamikle≈ütir
    
    @property(CCInteger)
    GRID_ROW: number = 0;

    protected start(): void {
        const grid = this.generateGrid();
        // Further grid management tasks can be handled here.
    }

    private generateGrid(): Node[][] {
        const grid: Node[][] = [];

        for (let i = 0; i < this.GRID_ROW; i++) {
            const row: Node[] = [];
            for (let j = 0; j < this.GRID_ROW; j++) {
                const piece = this.piecesPool.getPieceFromPool();
                const _position = this.getCenteredPosition(j,i);
                console.log(_position);
                
                piece.setPosition(_position);
                // Add click event listener
                piece.on(Node.EventType.TOUCH_END, this.onPieceClicked, this);

                row.push(piece);
            }
            grid.push(row);
        }

        return grid;
    }


    private onPieceClicked(event: EventTouch) {
        const clickedPiece = event.currentTarget as Node;
        console.log(`Piece clicked at position: ${clickedPiece.getPosition()}`);
        // Implement what happens when a piece is clicked.
    }

    private getCenteredPosition(j : number, i:number ) : Vec3{
      //EVERY game board should be square matrix
      const _j = (j * this.PIECE_CONTENT_SIZE) - ((this.PIECE_CONTENT_SIZE * 2) )
      const _i = (i * this.PIECE_CONTENT_SIZE) - ((this.PIECE_CONTENT_SIZE * 2) ) 
      return new Vec3(_j,_i)
    }
}
